[data-theme="dark"] {
  // Colors https://clrs.cc/
  --color-white: hsl(0, 0%, 100%);
  --color-black: hsl(0, 0%, 4%);
  --color-body-background: hsl(0, 0%, 21%);

  --color-light: #dddddd;
  --color-dark: hsl(0, 0%, 100%);
  --color-gray: #aaaaaa;

  --color-primary: #001f3f;
  --color-info: #7fdbff;
  --color-success: #2ecc40;
  --color-warning: #ffdc00;
  --color-danger: #85144b;

  // LINKs
  --color-link: #0074d9;
  --color-link-hover: #39cccc;
  --color-link-active: #3d9970;

  // Cards
  --color-border: var(--color-gray);

  // Icons
  --color-icon-default: white;
}
