$grid-columns: 12 !default;
$grid-spacing: 2%;

.grid {
  --grid-cols: #{$grid-columns};
  display: grid;
  // default no gap
  grid-gap: var(--grid-gap, 0);
  // grid of 12 columns
  grid-template-columns: repeat(var(--grid-cols), 1fr);

  > * {
    // each grid item takes full width by default
    grid-column-end: span var(--grid-cols);
  }
}

.grid-gap-null {
  --grid-gap: 0rem;
}
.grid-gap-xxxxs {
  --grid-gap: var(--space-xxxxs, 0.125rem);
}
.grid-gap-xxxs {
  --grid-gap: var(--space-xxxs, 0.25rem);
}
.grid-gap-xxs {
  --grid-gap: var(--space-xxs, 0.375rem);
}
.grid-gap-xs {
  --grid-gap: var(--space-xs, 0.5rem);
}
.grid-gap-sm {
  --grid-gap: var(--space-sm, 0.75rem);
}
.grid-gap-md {
  --grid-gap: var(--space-md, 1.25rem);
}
.grid-gap-lg {
  --grid-gap: var(--space-lg, 2rem);
}
.grid-gap-xl {
  --grid-gap: var(--space-xl, 3.25rem);
}
.grid-gap-xxl {
  --grid-gap: var(--space-xxl, 5.25rem);
}
.grid-gap-xxxl {
  --grid-gap: var(--space-xxxl, 8.5rem);
}
.grid-gap-xxxxl {
  --grid-gap: var(--space-xxxxl, 13.75rem);
}

@for $i from 1 through $grid-columns {
  .col-#{$i} {
    grid-column-end: span #{$i};
  }
}

.grid-auto-cols {
  display: grid;
  grid-gap: var(--grid-gap, 0);
  grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
}

.grid-auto-xs,
.grid-auto-sm,
.grid-auto-md,
.grid-auto-lg,
.grid-auto-xl {
  display: grid;
  grid-gap: var(-grid-gap, 0);
  grid-template-columns: repeat(auto-fit, minmax(var(--col-min-width), 1fr));
}

.grid-auto-xs {
  --col-min-width: 8rem;
}
.grid-auto-sm {
  --col-min-width: 10rem;
}
.grid-auto-md {
  --col-min-width: 15rem;
}
.grid-auto-lg {
  --col-min-width: 20rem;
}
.grid-auto-xl {
  --col-min-wdith: 25rem;
}

@for $i from 1 through $grid-columns {
  .col-start-#{$i} {
    grid-column-start: #{$i};
  }
  .col-end-#{$i} {
    grid-column-end: #{$i + 1};
  }
}

.col-end {
  grid-column-end: -1;
}

// breakpoints
$breakpoints: (
  xs: 32rem,
  sm: 48rem,
  md: 64rem,
  lg: 80rem,
  xl: 90rem
) !default;

@mixin breakpoint($breakpoint) {
  @media (min-width: map-get($map: $breakpoints, $key: $breakpoint)) {
    @content;
  }
}

@include breakpoint(xs) {
  .grid-auto-xs\@xs {
    --col-min-width: 8rem;
  }
  .grid-auto-sm\@xs {
    --col-min-width: 10rem;
  }
  .grid-auto-md\@xs {
    --col-min-width: 15rem;
  }
  .grid-auto-lg\@xs {
    --col-min-width: 20rem;
  }
  .grid-auto-xl\@xs {
    --col-min-width: 25rem;
  }

  .grid-auto-cols\@xs {
    grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
  }

  @for $i from 1 through $grid-columns {
    .col-#{$i}\@xs {
      grid-column-end: span #{$i};
    }
    .col-start-#{$i}\@xs {
      grid-column-start: #{$i};
    }
    .col-end-#{$i + 1}\@xs {
      grid-column-end: #{$i + 1};
    }
  }

  .col-start-auto\@xs {
    grid-column-start: auto;
  }
  .col-end-auto\@xs {
    grid-column-end: auto;
  }
}

