//
@function grid-width($cols, $has-gutter: true) {
  @if $has-gutter {
    @return calc(((100% / #{$grid-columns}) * #{$cols}) - #{$grid-spacing});
  } @else {
    @return calc((100% / #{$grid-columns}) * #{$cols});
  }
}

